import styled from "styled-components";

export const ShopcartItemCountBadge = styled.div`
  background-color: red;
  color: white;
  border-radius: 20px;
  position: absolute;
  top: -10px;
  left: -15px;
  font-size: 0.5em;
  padding: 5px;
  width: 1em;
  text-shadow: none;
`;

export const ShopcartIcon = styled.i`
  padding: 0 5px 0 10px;
  margin: auto;
  font-size: 200%;
  position: relative;
  color: white;
  text-shadow: 2px 2px 2px rgb(0, 0, 0);
  cursor: pointer;
`;
